import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

const noop = (event) => {
	event.preventDefault();
};

class Form extends React.Component {
	constructor(props) {
		super(props);
	}

	getChildContext() {
		return {
			form: this.props.form
		};
	}

	render() {
		const props = this.props;
		const {horizontal, inline, className, children, ...others } = props;
		const klassName = KlassName('form',{
				['form--inline']:('inline' in props && inline !== false ),
				['form--horizontal']:('horizontal' in props && horizontal !== false )
			  },className);

		return (
			<form
				{...others}
				className={klassName}
			>
				{children}
			</form>
		);
	}
};

Form.propTypes = {
	horizontal: PropTypes.bool,
	inline: PropTypes.bool,
	className: PropTypes.string,
	children: PropTypes.any,
	form: PropTypes.object,

	onSubmit: PropTypes.func
};

Form.childContextTypes = {
	form: PropTypes.object
};

Form.defaultProps = {
	onSubmit: noop
};

export default Form;